<template>
  <div>
    <div class="topTitle">
      <div class="leftTitle">用户管理</div>
      <!-- <div class="rightButton">
                <el-button type="primary" size="mini" >
                    添加命令</el-button>
            </div> -->
    </div>

    <div class="mainMseeage">
      <el-form :inline="true" :model="formInline" class="searchForm">
        <el-row :gutter="20">
          <el-col :span="6">
            <el-form-item label="微信id">
              <el-input
                v-model="formInline.wxid"
                placeholder="微信id"
                clearable
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="微信名称">
              <el-input
                v-model="formInline.wxName"
                placeholder="微信名称"
                clearable
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="京东pin">
              <el-input
                v-model="formInline.jdPtPin"
                placeholder="京东pin"
                clearable
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="状态">
              <el-select
                v-model="formInline.status"
                placeholder="请选择status"
                clearable
              >
                <el-option
                  v-for="item in statusOptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                >
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="等级">
              <el-input v-model="formInline.level" clearable></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item>
              <el-button type="primary" @click="onSubmit">查询</el-button>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>

      <el-table :data="tableData" v-loading="tableLoading" style="width: 100%">
        <el-table-column prop="wxid" label="微信id" align="center" width="100">
        </el-table-column>
        <el-table-column
          prop="wxName"
          label="微信名称"
          align="center"
          width="200"
        >
        </el-table-column>
        <el-table-column
          prop="wxpusheruid"
          label="wxpusheruid"
          align="center"
          width="120"
        >
        </el-table-column>
        <el-table-column prop="jdPtPinList" label="京东号列表" align="center">
          <template slot-scope="scoped">
            <div
              v-for="(item, index) in JSON.parse(
                JSON.stringify(scoped.row.jdPtPinList)
              )"
              :key="index"
            >
              {{ item }}
            </div>
          </template>
        </el-table-column>
        <el-table-column prop="" label="状态" align="center" width="80">
          <template slot-scope="scoped">
            {{ scoped.row.status | status }}
          </template>
        </el-table-column>
        <el-table-column
          prop="level"
          label="等级"
          align="center"
          width="80"
        ></el-table-column>

        <el-table-column label="操作" align="center" fixed="right" width="100">
          <template slot-scope="scoped">
            <el-button type="text" @click="upDate(scoped.row)">修改</el-button>
          </template>
        </el-table-column>
      </el-table>

      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pageNo"
        :page-sizes="[10, 20, 30]"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="tableTotal"
      >
      </el-pagination>
    </div>

    <!-- 修改 -->

    <el-dialog
      title="修改"
      :visible.sync="upDateVisible"
      width="40%"
      @close="upDateClose()"
    >
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" >
        <el-form-item label="微信id" prop="wxid">
          <el-input v-model="ruleForm.wxid" clearable></el-input>
        </el-form-item>
        <el-form-item label="微信名称" prop="wxName">
          <el-input v-model="ruleForm.wxName" clearable></el-input>
        </el-form-item>
        <el-form-item label="京东号列表" prop="jdPtPinList">
          <div v-for="(item, index) in ruleForm.jdPtPinList" :key="index">
            <el-input
              v-model="ruleForm.jdPtPinList[index]"
              clearable
              style="width: 90%"
            ></el-input
            ><el-button type="text" @click="addjdPtPinList(index)">+</el-button>
            <el-button type="text" @click="reducejdPtPinList(index)"
              >-</el-button
            >
          </div>
        </el-form-item>
        <el-form-item label="wxpusheruid" prop="wxpusheruid">
          <el-input v-model="ruleForm.wxpusheruid" clearable></el-input>
        </el-form-item>
        <el-form-item label="状态" prop="status">
          <el-select
            v-model="ruleForm.status"
            placeholder="请选择status"
            clearable
          >
            <el-option
              v-for="item in statusOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="等级" prop="level">
          <el-input v-model="ruleForm.level" clearable></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="upDateVisible = false">取 消</el-button>
        <el-button
          type="primary"
          :loading="sureLoading"
          @click="sureUpDate('ruleForm')"
          >确 定</el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { Message } from "element-ui";
import { queryUsers, updateUser } from "../axiosRequest/allRequest";
export default {
  filters: {
    status(value) {
      const obj = {
        0: "正常",
        1: "拉黑",
      };
      return obj[value];
    },
  },

  data() {
    return {
      statusOptions: [
        {
          value: 0,
          label: "正常",
        },
        {
          value: 1,
          label: "拉黑",
        },
      ],
      formInline: {
        wxid: "",
        wxName: "",
        jdPtPin: "",
        status: "",
        level: "",
      },
      tableData: [],
      tableLoading: false,
      pageNo: 1,
      pageSize: 10,
      tableTotal: 0,
      upDateVisible: false,
      sureLoading: false,
      ruleForm: {
        wxid: "",
        wxName: "",
        jdPtPinList: [],
        wxpusheruid: "",
        status: "",
        level: "",
        id: "",
      },
      rules: {
        wxid: [{ required: true, message: "请输入微信id", trigger: "blur" }],
        wxName: [
          { required: true, message: "请输入微信名称", trigger: "blur" },
        ],
        jdPtPinList: [
          { required: true, message: "请输入jdPtPinList", trigger: "blur" },
        ],
        wxpusheruid: [
          { required: true, message: "请输入wxpusheruid", trigger: "blur" },
        ],
        status: [{ required: true, message: "请选择状态", trigger: "blur" }],
        level: [{ required: true, message: "请输入level", trigger: "blur" }],
        id: [],
      },
      //   jdPtPinList: [],
    };
  },

  created() {
    this.onSubmit();
  },
  methods: {
    onSubmit() {
      this.tableLoading = true;
      let param = {
        ...JSON.parse(JSON.stringify(this.formInline)),
        pageNo: this.pageNo,
        pageSize: this.pageSize,
      };

      queryUsers(param).then((res) => {
        this.tableLoading = false;
        if (res.code != 200) {
          Message.error(res.message);
        } else {
          this.tableData = res.data.list;
          this.tableTotal = res.data.total;
        }
      });
    },

    handleSizeChange(val) {
      this.pageSize = val;
      this.onSubmit();
    },
    handleCurrentChange(val) {
      this.pageNo = val;
      this.onSubmit();
    },

    upDate(row) {
      this.upDateVisible = true;
      this.ruleForm.wxid = row.wxid;
      this.ruleForm.wxName = row.wxName;
      this.ruleForm.jdPtPinList = JSON.parse(JSON.stringify(row.jdPtPinList));
      this.ruleForm.wxpusheruid = row.wxpusheruid;
      this.ruleForm.status = row.status;
      this.ruleForm.level = row.level;
      this.ruleForm.id = row.id;
    },

    reducejdPtPinList(index) {
      if(this.ruleForm.jdPtPinList.length==1) return Message.warning('至少保留一个！')
      this.ruleForm.jdPtPinList.forEach((item, i) => {
        if (index == i) {
          this.ruleForm.jdPtPinList.splice(index, 1);
        }
      });
    },
    addjdPtPinList() {
      this.ruleForm.jdPtPinList.push("");
    },

    sureUpDate(formName) {
      // this.ruleForm.jdPtPinList= [123123]
      if(!this.ruleForm.jdPtPinList[0]) return Message.warning('京东号列表至少存在一个值！')
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.sureLoading = true;
          let params = {
            ...JSON.parse(JSON.stringify(this.ruleForm)),
          };
          updateUser(params).then((res) => {
            this.sureLoading = false;
            if (res.code == 200) {
              this.upDateVisible = false;
              Message.success("修改成功");
              this.onSubmit();
            } else {
              Message.error(res.message);
            }
          });
        } else {
          return false;
        }
      });
    },

    upDateClose() {
      this.ruleForm = {
        wxid: "",
        wxName: "",
        jdPtPinList: "",
        wxpusheruid: "",
        status: "",
        level: "",
      };
    },
  },
};
</script>

<style lang="less" scoped>
::v-deep .searchForm {
  .el-form-item__label {
    width: 70px;
  }
}
</style>